'use client';
import { useState } from 'react';
import { TableCard } from '@/components/tables/TableCard';
import { ScrollButtons } from '@/components/layout/ScrollButtons';
import { RegistrationScreen } from '@/components/layout/RegistrationScreen';

const TABLES = [
  { id: 1, price: 10, isActive: true },
  { id: 2, price: 20, isActive: false },
  { id: 3, price: 40, isActive: false },
  { id: 4, price: 80, isActive: false },
  { id: 5, price: 160, isActive: false },
  { id: 6, price: 320, isActive: false },
  { id: 7, price: 640, isActive: false },
  { id: 8, price: 1280, isActive: false },
  { id: 9, price: 2560, isActive: false },
  { id: 10, price: 5120, isActive: false },
  { id: 11, price: 10240, isActive: false },
  { id: 12, price: 20480, isActive: false },
];

export default function TablesPage() {
  const [showRegistration, setShowRegistration] = useState(true);

  if (showRegistration) {
    return <RegistrationScreen onComplete={() => setShowRegistration(false)} />;
  }

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 pb-24">
      <div className="sticky top-0 z-10 bg-white dark:bg-gray-800 shadow-md">
        <div className="container mx-auto px-4 py-4">
          <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
            Matrix TON - Tables
          </h1>
        </div>
      </div>

      <ScrollButtons />

      <div className="relative container mx-auto px-4 py-8">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {TABLES.map((table) => (
            <TableCard
              key={table.id}
              tableNumber={table.id}
              price={table.price}
              isActive={table.isActive}
            />
          ))}
        </div>
      </div>
    </div>
  );
}
